$cookie-color-text: $color-bg-1;
$cookie-color-bg: $color-text;

.cookie-consent {
  z-index: 100000;
  // display: block;
  // visibility: hidden;
  position: fixed;
  // left: $base-unit;
  right: $base-unit;
  // transform: translateX(-50%);
  bottom: $base-unit;
  min-width: calc(320px - #{$base-unit * 2});
  max-width: calc(100% - #{$base-unit * 2});
  width: $base-unit * 20;
  height: auto;
  font-size: ms(-1);
  line-height: ms-line-height(-2);
  text-align: left;
  background-color: $cookie-color-bg;
  color: $cookie-color-text;
  // border: 1px solid;
  box-shadow: 0 0 $base-unit rgba($cookie-color-text, .12);
  border-radius: $base-unit;
  padding: $base-unit * .5 $base-unit;
  opacity: .9;

  @supports (-webkit-backdrop-filter: blur(10px)) {
    -webkit-backdrop-filter: blur($base-unit);
    background-color: rgba($cookie-color-bg, .9);
    opacity: 1;
  }

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  @include min-screen($screen-m) {
    flex-direction: row;
    // justify-content: center;
    align-items: flex-end;
    text-align: left;
  }

  .cookie-consent__text {
    flex: 1;
  }

  .button {
    display: block;
    // margin-left: $base-unit * .5;
    // margin-bottom: 0;
    margin: 0 auto;
    margin-top: $base-unit * .25;
    text-transform: uppercase;
    font-size: ms(-2);
    line-height: ms-line-height(-2);
    background-color: $cookie-color-bg;
    color: $cookie-color-text;
    border-color:$cookie-color-text;

    &:hover {
      background-color: $cookie-color-text;
      color: $cookie-color-bg;
    }
  }

  @include min-screen($screen-m) {
    flex-direction: row;
    // justify-content: center;
    align-items: flex-end;
    text-align: left;
    padding: $base-unit;

    .button {
      margin: 0 auto;
    }
  }
}

