@import 'cookie-consent';

hr {
  border-color: $bg-color-2;
  margin-top: 0;
}

body {
  background-color: $color-bg-0;
}

.section__inner {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: $base-unit * 3 $base-unit;

  @include mq($min-width: 1280px) {
    width: 70%;
    max-width: 1200px;
  }
}

h1,
h2 {
  font-weight: bold;
}
h1 {
  line-height: $base-unit * 1.75;
  margin-top: 0;
}

.section--logo {
  background: $color-bg-1;
  // background: #fff;

  .section__inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: $base-unit $base-unit * 0.5 0;

    @include min-screen($screen-m) {
      padding: ($base-unit * 1.5) $base-unit 0;
    }
  }

  .logo {
    color: currentColor;

    svg {
      fill: currentColor;
      height: $base-unit * 1.25;
      vertical-align: middle;
    }
  }

  a {
    font-size: ms(-1);
  }

  svg.svg-icon {
    width: $base-unit * 0.72;
    height: $base-unit * 0.72;
    vertical-align: middle;
    margin-top: $base-unit * -0.125;
    margin-left: $base-unit * 0.25;
    fill: currentColor;
  }
}

.navigation {
  > :not(:last-child) {
    margin-right: $base-unit * 0.75;
  }

  .button {
    border-color: $color-red;
    box-shadow: none;
    margin-bottom: 0;
  }

  @include max-screen($screen-m) {
    .button {
      display: none;
    }
  }
}

