.hype {
  padding: $base-unit * 0.5 $base-unit * 1.5;
  border-radius: $base-unit * 0.5;
  background-color: white;
  display: inline-block;
  font-size: $font-size-sans-1;
  // font-weight: bold;
  margin: $base-unit auto;
  text-align: center;
}

.image {
  border-radius: $base-unit * 0.5;
  box-shadow: 0 0 $base-unit rgba($color-text, 0.2);
  overflow: hidden;
}

.section--name {
  font-size: ms(-1);
  text-transform: uppercase;
}

#page-homepage {
  .section--hero {
    @include min-screen($screen-m) {
      text-align: left;
    }
  }
}

.section--hero {
  // background: linear-gradient(to bottom, $bg-color-1 10%, rgba($bg-color-1, 0) 100%);
  background: $color-bg-1;
  // background: #fff;
  // color: $bg-color-1;
  overflow: hidden;
  position: relative;
  text-align: center;

  .section__inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  .section__item {
    &:nth-child(1) {
      flex: 5;
      position: relative;
      z-index: 1;

      @include min-screen($screen-m) {
        margin-right: $base-unit;
      }
      @include min-screen($screen-xxl) {
        flex: 4;
      }
    }

    &:nth-child(2) {
      flex: 5;

      @include max-screen($screen-m) {
        position: absolute;
        bottom: $base-unit * 2 * -1;
        right: $base-unit;
        width: 50%;
      }

      @include max-screen($screen-s) {
        bottom: $base-unit * 6.5 * -1;
        left: 50%;
        transform: translateX(-50%);
        right: initial;
        width: 100%;
      }
    }
  }
}

.section--alert,
.section--news,
.section--notice {
  text-align: center;

  .section__inner {
    padding: $base-unit * 0.5 $base-unit;
  }
}

.section--alert {
  background-color: $color-primary;
  color: $color-bg-1;

  a {
    color: lighten($action-color, 10);
  }

  span {
    color: $color-bg--2;
    font-size: $font-size-sans-0;
    letter-spacing: 0.05em;
    margin-right: $base-unit * 0.25;
    text-transform: uppercase;
  }
}

.section--news {
  background-color: $color-text;
  color: $color-bg-1;

  a {
    color: lighten($action-color, 10);
  }

  span {
    color: $color-bg--2;
    font-size: $font-size-sans--1;
    letter-spacing: 0.05em;
    margin-right: $base-unit * 0.25;
    text-transform: uppercase;
  }
}

.section--notice {
  background-color: $color-bg-1;
  font-size: $font-size-sans--1;

  + .section--catalog {
    box-shadow: none;
  }
}

