.section--about {
  // background-color: $color-bg-0;
  color: $color-text;
  // background: linear-gradient(to top, $color-bg-1 0%, rgba($color-bg-1, 0) 90%);
  // background: linear-gradient(135deg, $bg-color-2 0%, #5352A6 100%);
  // background-color: #5352A6;
  // color: $bg-color-1;

  @include min-screen($screen-m) {
    text-align: center;
  }

  &.section--about-1 {
    // background: linear-gradient(to bottom, $color-bg-1 0%, rgba($color-bg-1, 0) 90%);
  }
  &.section--about-2 {
    // background: linear-gradient(
    //   to top,
    //   $color-bg-1 0%,
    //   rgba($color-bg-1, 0) 90%
    // );
    // margin-top: $base-unit * -2;
  }

  .section__item--text {
    max-width: 80ch;
    margin: 0 auto;
  }

  .section__item--blurps {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    // padding: $base-unit 0
    // font-size: $font-size-sans--1;
    line-height: $line-height--1;
    text-align: center;

    > * {
      flex: 1 0 300px;
      max-width: 400px;
      margin: $base-unit;
    }

    p:last-child {
      // text-align: left;
      font-size: $font-size-sans--2;
