}



#activities {
  --images-size: 162vmax;

  max-width: 100%;
  overflow-x: hidden;

  .section__images {
    position: relative;
    display: flex;
    justify-content: center;
    width: var(--images-size);
    transform: translateX(calc((var(--images-size) - 100vw) / 2 * -1));
    transform-origin: center;
    margin: $base-unit * -0.5 auto $base-unit * 5;

    @include min-screen($screen-m) {
      --images-size: 138vmax;

      &:hover {
        .button {
          display: block;
        }
        .image {
          transform: none;
        }

        img {
          opacity: 0.62;
        }
      }

      .image {
        transition: transform 0.2s;
      }

      .button {
        display: none;
      }

      img {
        opacity: 1;
      }
    }
  }

  .image {
    border-radius: $base-unit * 0.25;
    margin: 0 $base-unit * 0.5;
    // background-color: $color-orange;

    // @for $i from 1 through 6 {
    //   &:nth-child(#{$i}) {
    //     transform: translateY(#{($i - 1) * 5%});
    //   }
    // }

    // transform: translateY(5%);

    // &:nth-child(odd) {
    // &:first-of-type {
    &:first-of-type,
    &:last-of-type {
      transform: translateY(10%);
    }

    // &:last-of-type {
    //   transform: translateY(20%);
    // }
  }

  img {
    width: 100%;
    height: auto;
    opacity: 0.62;
  }

  .button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-bottom: 0;
  }
}

.section--lms {
  position: relative;
  // background-color: mix($color-primary, $bg-color, 62);
  background-color: $color-primary;
  // background: linear-gradient(325deg, darken(desaturate($color-primary, 20), 20) 0%, $color-primary 100%);
  background: linear-gradient(
    325deg,
    mix($color-primary, $color-text, 50) 0%,
    $color-primary 100%
  );
  box-shadow: 0 0 $base-unit * 3 rgba($color-text, 0.38);
  color: $bg-color;
  text-align: center;

  .hype {
    background-color: lighten($color-primary, 10);
  }

  .badge {
    background-color: #d4495e;
    padding: math.div($base-unit, 3) $base-unit * 0.5 $base-unit * 0.25;
    text-transform: uppercase;
    position: absolute;
    top: math.div($base-unit, 1.5) * -1;
    left: 50%;
    transform: translateX(-50%);
    border-radius: $base-unit * 0.5;
    font-weight: 700;
    font-size: $font-size-sans--1;
    line-height: $line-height--2;
    letter-spacing: 0.05em;
  }

  .section__inner {
    width: 100%;
    max-width: initial;
    // padding: $base-unit * 2 2px 2px;
    padding-bottom: 0;
  }

  .section__item--title {
    margin-bottom: $base-unit * 2;
    text-align: center;

    > * {
      margin: 0 auto $base-unit;
      max-width: 90ch;
    }

    p {
      max-width: 70ch;
      font-size: $font-size-sans--1;
      line-height: $line-height--2;
    }
  }

  .screenshot-cta {
    position: relative;
    display: block;

    &:not(:last-child) {
      margin-bottom: $base-unit;
    }

    .button {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      margin-bottom: 0;
    }

    img {
      width: 100%;
      height: auto;
      opacity: 0.8;
    }

    @include min-screen($screen-m) {
      width: 50vw;

      &:hover {
        .button {
          display: block;
        }

        img {
          opacity: 0.62;
        }
      }

      .button {
        display: none;
      }

      img {
        opacity: 1;
      }
    }
  }

  #screenshots-catalog-timline {
    margin-bottom: $base-unit * 3;

    @include max-screen($screen-m) {
      .button {
        transform: translate(-50%, -50%);
      }
    }

    @include min-screen($screen-m) {
      display: flex;

      .screenshot-cta {
        &:first-child {
          transform: translateX(-10%);
        }

        &:last-child {
          transform: translateX(10%);
        }
      }
    }
  }

  #screenshots-lms {
    display: flex;
    justify-content: center;
    margin-top: $base-unit * -1;
    overflow: hidden;

    .screenshot-cta {
      transform: translateY(11%);
    }

    @include min-screen($screen-m) {
      margin-top: $base-unit * -2;

      .button {
        display: block;
        top: $base-unit * -1;
        transform: translateX(-50%);
      }
    }
  }
}

#logo-tacr,
#logo-cro {
  display: block;
  fill: currentColor;
  margin: 0 auto;
}

#logo-tacr {
  height: $base-unit * 5;
}
#logo-cro {
  height: $base-unit;
}

.section--partners {
  .section__item {
    margin-bottom: $base-unit;
  }

  p {
    max-width: $base-unit * 35;
    // margin: 0 auto $base-unit;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
}

.section--additional {
  background-color: $pharlap-contrast-AA;
  color: $bg-color;

  a {
    color: currentColor;
  }

  .section__inner {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  .section__item {
    flex: 1 25ch;

    &:not(.section__item--title) {
      white-space: nowrap;
    }
  }

  .section__item--title {
    text-align: center;
    flex: 2 60ch;
  }
}

.section--blog {
  article {
    position: relative;
    max-width: 70ch;
    margin: 0 auto $base-unit * 2;

    &:not(:first-child) {
      padding-top: $base-unit * 2;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        width: 38%;
        transform: translateX(-50%);
        border-top: 1px solid $color-bg--1;
      }
    }
  }
}
